import speakPromise from '../index.js';

document.querySelector('form#promise').onsubmit = e => {
  e.preventDefault();
  let input = e.currentTarget.querySelector('input[type=text]');
  speakPromise(new SpeechSynthesisUtterance(input.value)).then(() => {
    alert('Actually Done :)');
  });
};

document.querySelector('form#vanilla').onsubmit = e => {
  e.preventDefault();
  let input = e.currentTarget.querySelector('input[type=text]');
  speechSynthesis.speak(new SpeechSynthesisUtterance(input.value));
  alert('Not really done :(');
};
